body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.img-content img{
    display: block;
    width: 300px;
    height: 450px;
    box-shadow: 0 0 4px rgba(0,0,0,0.2);
    border-radius: 4px;
}

.card-image {
    width: 220px;
    height: 220px;
}

.movie-title {
    font-weight: bold;
    font-size: xx-large;
}

.headers {
    font-weight: bold;
    font-size: large;
}

.center {
    float: none;
    margin-left: auto;
    margin-right: auto;
}

.genre-button {
    white-space: normal;
}
.div-background {
    fontFamily: sans-serif;
    width: 100%;
    height: 100%;
    background-color: #f7f7f7;
    background-size: cover;
}
.card-image {
    width: 5px;
    height: 5px;
}

